@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.card-grid {
    --padding: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 250px));
    padding: var(--padding);
    grid-gap: 1rem;
    gap: 1rem;
}

.card-grid.product {

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    padding: var(--padding);
    grid-gap: 1rem;



}

.detailsContainer {

    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.detailsContainer>* {
    margin: 1rem;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2);
    height: 300px;
    border-radius: .25rem;
    cursor: pointer;

    overflow: hidden;


}

.card-image {
    padding: 0;

}

.card:hover>.card-image>img {


    transform: scale(1.1);
}





.card-image>img {

    display: block;
    width: 95%;
    aspect-ratio: 16/13;
    object-fit: contain;
    max-height: 300px;
    min-height: 300px;
    object-position: center;
    transition: 200ms transform ease-in-out;

}

.card-footer {
    font-size: 13px;
    font-weight: 700;
    margin-top: 1rem;
    /* padding: var(--padding); */
    padding-top: 0;




}



.productPrice {
    font-weight: normal;
    margin-top: 0.5rem;

}
.cart-container-box {
    --padding: 1rem;
    display: flex;
    flex-direction: column;
    padding: var(--padding);
    grid-gap: 1rem;
    gap: 1rem;
}



.detailsContainer {

    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.detailsContainer>* {
    margin: 1rem;
}

.cart-card {
    display: flex;

    background: white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .2);
    /* overflow: hidden; */
    padding-left: 1rem;
    border-radius: .25rem;
    cursor: pointer;

    margin-bottom: 1rem;
    padding-top: 1rem;
    ;


}

.cart-image {
    padding: 0;
    margin-right: 1rem;
}

.card:hover>.cart-image>img {


    transform: scale(1.1);
}


.cart-link {
    width: 5rem;
    display: block;
}


.cart-link>img {

    /* display: block; */

    width: 100%;
    max-width: 100%;

    vertical-align: middle;
    height: auto;



    transition: 200ms transform ease-in-out;

}




.productPrice {
    font-weight: normal;
    margin-top: 0.5rem;

}
.section-header {
    position: relative;

    background-color: white;
}

.categories {

    position: absolute;
    width: 100%;
    top: 0%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: white;
    height: auto;
    left: 0;
    right: 0;
    z-index: -6;
}

.categories.active {
    border-top: 1px solid;
    top: 100%
}

.cart-container {

    background-color: aliceblue;
    width: 400px;
    z-index: 1000;
    height: 100vh;
    /* display: flex; */
    position: fixed;
    top: 0;
    right: -100%;
    transition: 850ms;
    overflow: scroll;
    padding-top: 1rem;
    padding-left: 1rem;
    overflow-x: hidden;
}

.cart-container.active {
    right: 0;
    transition: 250ms;
}


.cart-container>ul {
    list-style: none;
}
.nav-container {
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    border-bottom: solid 1px;
    padding-bottom: 1rem;
    align-items: center;
    position: relative;
}

.App {


    display: flex;
    flex-direction: column;


}

.App-content {

    flex: 1 1;
}

.banner {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    /* align-items: center; */
    /* justify-content: center; */
}

.hero-1 {
    margin-bottom: 2rem 0;
}

.hero-image>img {
    width: 100%;
    height: auto;

    display: block;




    object-position: center;
    transition: 200ms transform ease-in-out;
}
*, *::after, *::before {

    font-family: 'Open Sans', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}


body, html {
    height: 100%;
}

nav {
    text-decoration: none;
    text-transform: uppercase;
}

a {
    text-decoration: none;

}

a:visited {
    color: black;
}



.btn {
    width: 100%;
    padding: 1rem;
    color: white;
    background-color: black;
    font-weight: bold;
    font-size: 13px;
    border-radius: 2rem;
    text-transform: uppercase;

}
